import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  { 
    path: '',
    children: [
      {
        path: '',
        loadChildren: './tabs/tabs.module#TabsPageModule',
      }
    ]
  },
  {
    path: 'coin',
    children: [
      {
        path: '',
        loadChildren: './tabs/tabs.module#TabsPageModule'
      },
      {
        path: ':itemId',
        loadChildren: './item-view/item-view.module#ItemViewPageModule'
      }
    ]
  },
  { path: 'add-coin', loadChildren: './item-add/item-add.module#ItemAddPageModule' },
  // { path: 'item-view', loadChildren: './list/item-view/item-view.module#ItemViewPageModule' }
];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
