import * as moment from 'moment';

export function log(message: string, timestamp: number, storage: any) {
    storage.get('log')
        .then(val => save(formatLog(val, message)))
        .catch(() => save(formatLog(initLog(), message)));

    function initLog() {
        return [];
    }

    function formatLog(log, message) {
        // restrict logger to 100 entries
        while(log.length > 100) {
            log.shift();
        }

        log.push(`${moment(timestamp).format('ddd HH:mm')}: ${message}`);
        return log;
    }


    function save(log) {
        storage.set(`log`, log);
    }
}