import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../environments/environment';
import { Storage } from '@ionic/storage';
import { log } from './util/log';
import { marketsOpen } from 'precious-metals-markets';

@Injectable({
  providedIn: 'root'
})
export class MarketsService {
  marketStatus = marketsOpen();
  public marketUpdateInterval: number;
  logEnabled: boolean;

  constructor(
    private storage: Storage
  ) {
    this.marketUpdateInterval = environment.intervals.MARKET_SERVICE;
    this.logEnabled = environment.logEnabled;
  }

  marketsOpen(): Observable<boolean> {
    return new Observable(observer => {
      const res = marketsOpen();
      observer.next(res);

      setInterval(() => {
        const res = marketsOpen();
        observer.next(res);
        log(`Markets are open: ${res}`, Date.now(), this.storage);
      },
      this.marketUpdateInterval);
    });
  }
}
