import { Component } from "@angular/core";
import { Platform } from "@ionic/angular";
import { SplashScreen } from "@ionic-native/splash-screen/ngx";
import { StatusBar } from "@ionic-native/status-bar/ngx";
import { MetalsSpotService } from "./metals-spot.service";
import { StorageService } from "./storage.service";
import { MarketsService } from "./markets.service";
import { interval } from "rxjs";
import _map from "lodash/map";
import { environment } from "../environments/environment";

@Component({
  selector: "app-root",
  templateUrl: "app.component.html"
})
export class AppComponent {
  subGoldSilver: any;
  SPOT_REFRESH: number;
  marketStatus: boolean;
  subscriptionMarkets: any;

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private metalsSpotService: MetalsSpotService,
    private storageService: StorageService,
    private marketsService: MarketsService
  ) {
    this.initializeApp();
    this.SPOT_REFRESH = environment.intervals.SPOT_REFRESH;
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.splashScreen.hide();
      this.statusBar.styleDefault();

      // default status bar doesn't work android, bug still not fixed yet
      if (this.platform.is("android")) {
        this.statusBar.backgroundColorByHexString("#ffffff");
      }
    });
  }

  ngOnInit() {
    this.subscriptionMarkets = this.marketsService.marketsOpen().subscribe(res => {
      res ? this.marketStatus = true : this.marketStatus = false;
    });

    this.marketStatus && this.getSpot();
    const i = interval(this.SPOT_REFRESH);
    i.subscribe(() => this.marketStatus && this.getSpot() );
  }

  getSpot() {
    this.subGoldSilver = this.metalsSpotService
      .getMetalsLiveSpot()
      .subscribe(res => {
        this.storageService.saveRate(
          "xau",
          _map(res, "gold").filter(Boolean)[0]
        );
        this.storageService.saveRate(
          "xag",
          _map(res, "silver").filter(Boolean)[0]
        );
        this.storageService.saveRate(
          "xpt",
          _map(res, "platinum").filter(Boolean)[0]
        );
        this.storageService.saveRate(
          "xpd",
          _map(res, "palladium").filter(Boolean)[0]
        );
      });
  }
}
