import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../environments/environment';
import { log } from './util/log';
import { Storage } from '@ionic/storage';

@Injectable({
  providedIn: 'root'
})
export class NetworkStatusService {
  networkStatusTime: any;
  logEnabled: boolean;

  constructor(
    private storage: Storage
  ) {
    this.networkStatusTime = environment.intervals.NETWORK_REFRESH;
    this.logEnabled = environment.logEnabled;
  }

  online(): Observable<boolean> {
    return new Observable(observer => {
      const res = navigator.onLine;
      observer.next(res);

      setInterval(() => {
        const res = navigator.onLine;

        if(res === false && this.logEnabled === true) {
          log('Device is offline', Date.now(), this.storage);
        }

        observer.next(res);
      },
      this.networkStatusTime);
    });
  }
}
