import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { environment } from '../environments/environment';
import { log } from './util/log';

@Injectable({
  providedIn: 'root'
})
export class StorageService {
  private spotPricesTime: number;
  spotLocalStoreLimit: number;
  log: any;

  constructor(
    private storage: Storage
  ) {
    this.spotPricesTime = environment.intervals.SPOT_REFRESH;
    this.spotLocalStoreLimit = environment.localStorage.spotStoreLimit;
    this.log = log;
  }

  // getSpot() {
  //   this.storage.get('spot_xau').then(val => console.log(val) );
  //   this.storage.get('spot_xag').then(val => console.log(val) );
  //   this.storage.get('spot_xpt').then(val => console.log(val) );
  //   this.storage.get('spot_xpd').then(val => console.log(val) );
  // }

  saveRate(metalType, data) {
    // Initial setup of the store data schema
    this.offlineStoreFirstRun(metalType);

    this.storage.get(`spot_${metalType}`).then(val => {
      let update = val;
      update = JSON.parse(update);
      update.unshift({'timestamp': Date.now(), 'price': data});

      while(update.length > this.spotLocalStoreLimit) {
        update.pop();
      }

      this.storage.set(`spot_${metalType}`, JSON.stringify(update));
      this.storage.set(`spot_${metalType}_rate`, data);
      this.storage.set(`spot_${metalType}_timestamp`, Date.now());
    })
  }

  offlineStoreFirstRun(metalType: string) {
    this.storage.get(`spot_${metalType}`).then(val => {
      !val && this.storage.set(`spot_${metalType}`, JSON.stringify([]));
    });
  }

  callStorage() {
    // TODO, fallback to API if nothing is in storage
    // this.log('[storage.service.ts] Getting spot rates from storage', Date.now(), this.storage);
    // this.storage.get('spot_rate_xau').then(val => this.goldSpot = val ).catch(err => { this.goldSpot = 0 });
    // this.storage.get('spot_rate_xag').then(val => this.silverSpot = val ).catch(err => { this.silverSpot = 0 });
    // this.storage.get('spot_rate_xpt').then(val => this.platinumSpot = val ).catch(err => { this.platinumSpot = 0 });
    // this.storage.get('spot_rate_xpd').then(val => this.palladiumSpot = val ).catch(err => { this.goldSpot = 0 });
  }
}
