import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../environments/environment';
import { XauXag, Pall, Plat } from './spot-response-models';
import { Storage } from '@ionic/storage';
import * as moment from 'moment';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}

@Injectable({
  providedIn: 'root'
})
export class MetalsSpotService {
  private currencyconverterapi: string;
  private quandl: string;
  range = `&start_date=${moment(Date.now()).subtract(3, 'd').format('YYYY-MM-DD')}&end_date=${moment(Date.now()).format('YYYY-MM-DD')}`;
  logEnabled: boolean;

  constructor(
    private http: HttpClient,
    private storage: Storage
  ) {
    this.currencyconverterapi = environment.apiTokens.currencyconverterapi;
    this.quandl = environment.apiTokens.quandl;
    this.logEnabled = environment.logEnabled;
  }

  getMetalsLiveSpot(): Observable<{}> {
    const url = `https://api.metals.live/v1/spot`;
    return this.http.get<{}>(url);
  }

  getGoldSilverSpot(): Observable<{}> {
    // const url = `http://free.currencyconverterapi.com/api/v5/convert?q=XAU_USD&compact=y&apiKey=${this.currencyconverterapi}`;
    // const url = `https://free.currconv.com/api/v7/convert?apiKey=${this.currencyconverterapi}&q=XAU_USD,XAG_USD`;
    const url = `https://free.currconv.com/api/v7/convert?apiKey=${this.currencyconverterapi}&q=XAU_USD,XAG_USD&compact=ultra`;
    return this.http.get<XauXag>(url);
  }

  getPalladiumSpot(): Observable<{}> {
    const url = `https://www.quandl.com/api/v3/datasets/LPPM/PALL.json?api_key=${this.quandl}${this.range}`;
    return this.http.get<Pall>(url);
  }

  getPlatinumSpot(): Observable<{}> {
    const url = `https://www.quandl.com/api/v3/datasets/LPPM/PLAT.json?api_key=${this.quandl}${this.range}`;
    return this.http.get<Plat>(url);
  }
}
