import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl, ReactiveFormsModule } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { Components } from '@ionic/core';
import * as countryCodeLookup from 'country-code-lookup';

@Component({
  selector: 'app-new-item',
  templateUrl: './new-item.component.html',
  styleUrls: ['./new-item.component.scss'],
})
export class NewItemComponent implements OnInit {
  @Input() modal: Components.IonModal;
  closeButtonIcon: string;
  saveButtonIcon: string;
  countriesList: {};

  returnedData: any;

  newItemFormGroup: FormGroup;

  title: AbstractControl;
  type: AbstractControl;
  form: AbstractControl;
  weight: AbstractControl;
  purity: AbstractControl;
  notes: AbstractControl;
  quantity: AbstractControl;
  paid: AbstractControl;
  label: AbstractControl;
  labelColor: AbstractControl;
  cert: AbstractControl;
  certVendor: AbstractControl;
  country: AbstractControl;

  constructor(
    private formBuilder: FormBuilder,
    private modalController: ModalController,
  ) {
    this.newItemFormGroup = formBuilder.group({
      title: ['', Validators.required],
      type: ['silver', Validators.required],
      form: ['coin', Validators.required],
      weight: ['1', Validators.required],
      purity: ['0.999', Validators.required],
      quantity: ['1', Validators.required],
      paid: ['', Validators.required],
      label: [],
      labelColor: ['success'],
      cert: [],
      certVendor: ['pcgs'],
      notes: [],
      country: []
    });

    this.title = this.newItemFormGroup.controls['title'];
    this.type = this.newItemFormGroup.controls['type'];
    this.form = this.newItemFormGroup.controls['form'];
    this.weight = this.newItemFormGroup.controls['weight'];
    this.purity = this.newItemFormGroup.controls['purity'];
    this.notes = this.newItemFormGroup.controls['notes'];
    this.quantity = this.newItemFormGroup.controls['quantity'];
    this.paid = this.newItemFormGroup.controls['paid'];
    this.label = this.newItemFormGroup.controls['label'];
    this.labelColor = this.newItemFormGroup.controls['labelColor'];
    this.cert = this.newItemFormGroup.controls['cert'];
    this.certVendor = this.newItemFormGroup.controls['certVendor'];
    this.country = this.newItemFormGroup.controls['country']
  }

  onSubmit() {
    const data = {
      title: this.title.value,
      type: this.type.value,
      form: this.form.value,
      weight: this.weight.value,
      purity: this.purity.value,
      notes: this.notes.value,
      quantity: this.quantity.value,
      paid: this.paid.value,
      label: this.label.value,
      labelColor: this.labelColor.value,
      cert: this.cert.value,
      certVendor: this.certVendor.value,
      country: this.country.value
    }
    this.returnedData = data;
    // console.log(`submitting`, data);

    this.dismissModal();
  }

  dismissModal = () => this.modal.dismiss(this)

  ngOnInit() {
    this.closeButtonIcon = 'close';
    this.saveButtonIcon = 'checkmark';
    this.countriesList = countryCodeLookup.countries;
  }

}
