import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Item } from './collection-item.model';
import { environment } from '../environments/environment';
import { Storage } from '@ionic/storage';
import { FakeCollection } from './fake-collection';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}

@Injectable({
  providedIn: 'root'
})

export class CollectionService {
  private fakeCollection = FakeCollection;
  private currencyconverterapi: string;
  private quandl: string;

  private collection: Item[] = this.fakeCollection;

  constructor(
    private http: HttpClient,
    private storage: Storage
  ) {
    this.currencyconverterapi = environment.apiTokens.currencyconverterapi;
    this.quandl = environment.apiTokens.quandl;
  }

  getAllItems() {
    return this.collection;
  }

  getItem(itemId: string) {
    return {...this.collection.find(item => {
      return item.id === itemId;
    })}
  }

  deleteItem(itemId: string) {
    this.collection = this.collection.filter(item => {
      return item.id !== itemId;
    })
  }

  filterItems(searchString: string) {
    return this.collection.filter(item => {
      return item.title.toLowerCase().indexOf(searchString) >= 0;
    })
  }

  getGoldSpot(): Observable<[]> {
    const url = `http://free.currencyconverterapi.com/api/v5/convert?q=XAU_USD&compact=y&apiKey=${this.currencyconverterapi}`;
    return this.http.get<[]>(url);
  }

  getSilverSpot(): Observable<[]> {
    const url = `http://free.currencyconverterapi.com/api/v5/convert?q=XAG_USD&compact=y&apiKey=${this.currencyconverterapi}`;
    return this.http.get<[]>(url);
  }

  getPalladiumSpot(): Observable<[]> {
    const url = `https://www.quandl.com/api/v3/datasets/JOHNMATT/PALL.json?api_key=${this.quandl}&start_date=`;
    return this.http.get<[]>(url);
  }

  getPlatinumSpot(): Observable<[]> {
    const url = `https://www.quandl.com/api/v3/datasets/JOHNMATT/PLAT.json?api_key=${this.quandl}&start_date=`;
    return this.http.get<[]>(url);
  }
}
