import { Component, OnInit } from '@angular/core';
import { MarketsService } from '../../markets.service';
import { NetworkStatusService } from '../../network-status.service';

@Component({
  selector: "app-markets",
  templateUrl: "./markets.component.html",
  styleUrls: ["./markets.component.scss"]
})

export class MarketsComponent implements OnInit {
  marketStatus: any;
  marketSub: any;
  networkSub: any;
  deviceOnline: boolean;

  constructor(
    private marketsService: MarketsService,
    private networkStatusService: NetworkStatusService,
  ) {
  }

  ngOnInit() {
    this.marketSub = this.marketsService.marketsOpen().subscribe(res => {
        res ? this.marketStatus = 'open' : this.marketStatus = 'closed';
    });

    // this doesn't work in airplane mode for some reason
    this.networkSub = this.networkStatusService.online().subscribe(res => {
        this.deviceOnline = res;
    });
  }
}
