import { Component, OnInit } from '@angular/core';
import { CollectionService } from '../../collection.service';
import { MetalsSpotService } from '../../metals-spot.service';
import { MarketsService } from '../../markets.service';
import { Storage } from '@ionic/storage';
import { Observable } from 'rxjs';
import { interval } from 'rxjs';
import { Xau } from '../../spot-response-models';
import { environment } from '../../../environments/environment';
import { log } from '../../util/log';

@Component({
  selector: 'app-spot-prices',
  templateUrl: './spot-prices.component.html',
  styleUrls: ['./spot-prices.component.scss'],
})
export class SpotPricesComponent implements OnInit {
  goldSpot: number; // grab from localStorage so UI isn't empty while querying from API
  silverSpot: number;
  palladiumSpot: any;
  platinumSpot: any;
  private SPOT_REFRESH_STORAGE: number;
  spotLocalStoreLimit: number;

  subGoldSilver: any;
  subPal: any;
  subPla: any;

  subscriptionMarkets: any;
  marketStatus: boolean;

  log: any;

  constructor(
    private collectionService: CollectionService,
    private metalsSpotService: MetalsSpotService,
    private marketsService: MarketsService,
    private storage: Storage
  ) {
    this.log = log;
    this.SPOT_REFRESH_STORAGE = environment.intervals.SPOT_REFRESH_STORAGE;
    this.spotLocalStoreLimit = environment.localStorage.spotStoreLimit;
  }

  ngOnInit() {
    this.subscriptionMarkets = this.marketsService.marketsOpen().subscribe(res => {
      res ? this.marketStatus = true : this.marketStatus = false;
    });

    this.callStorage();

    // TODO run this.refreshRateNeeded('xau', Date.now()); to see if refresh is needed
    const i = interval(this.SPOT_REFRESH_STORAGE);
    // const i = interval(1999);

    i.subscribe(() => {
      if(this.marketStatus) {
        this.callStorage();
      }
    });
  };

  callStorage() {
    // TODO, fallback to API if nothing is in storage
    this.log('[spot-prices.component.ts] Getting spot rates from storage', Date.now(), this.storage);
    this.storage.get('spot_xau_rate').then(val => this.goldSpot = val ).catch(err => { this.goldSpot = 0 });
    this.storage.get('spot_xag_rate').then(val => this.silverSpot = val ).catch(err => { this.silverSpot = 0 });
    this.storage.get('spot_xpt_rate').then(val => this.platinumSpot = val ).catch(err => { this.platinumSpot = 0 });
    this.storage.get('spot_xpd_rate').then(val => this.palladiumSpot = val ).catch(err => { this.goldSpot = 0 });
  }
}
